<template>
  <div class="set-pay">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>支付中心</span>
    </header>
    <ul>
      <li class="pay-pass" @click="changePass">
        修改支付密码
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data () {
      return {

      }
    },
    methods: {
      changePass() {
        this.$router.push('/payword/1');
      },
      back() {
        this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  .set-pay {
    height: 100vh;
    background-color: #f2f2f2;
  }
  .pay-pass:first-child {
    margin-top: 0.2rem;
  }
  .pay-pass {
    padding: 0 0.2rem;
    height: 0.88rem;
    line-height: 0.88rem;
    background-color: #ffffff;
    border-bottom: 1px solid #f2f2f2;
  }
</style>



